<template>
    <div>
      <van-nav-bar
        title=""
        left-text=""
        left-arrow
        @click-left="$router.go(-1)"
    />  
    <van-cell-group style="margin-top:15px">
        <van-field v-model="user" placeholder=""/>
        <van-field type="password" v-model="pwd" placeholder=""/>
    </van-cell-group>
        <div style="margin:15px">
            <van-button color="#f30" block round @click="login"></van-button>
            <br>
            <van-button color="#f30" block round @click="$router.push('/zhuce')"></van-button>
        </div>
    </div>
</template>
<script>
import Cookie from '../assets/js/Cookie'
export default {
    data(){return {user:"",pwd:""}},
    methods:{
        login(){
            this.$http.get('http://127.0.0.1/deng.php',{   //params
                            params:{
                                user:this.user,
                                pwd:this.pwd
                            }
                        })    
                        .then(res => {
                           if(res.data==""){
                               alert("");
                               console.log(res.data);
                           }else if(res.data==""){
                                alert("");
                                console.log(res.data);
                           }else{
                                 alert("");
                                 Cookie.setCookie("uname",this.user);
                                 this.$router.replace('/mine')
                           }
                        })
                        .catch(res => {
                            console.log('getAjax,',res)
                    })
             },
    }
}
</script>